import "./key";
import './App.css';
import React, { useState } from 'react';
import Axios from "axios";

function App() {
  const [query, setQuery] = useState(""); 
  const [recipes, setRecipes] = useState([]); 
  const [isLoading, setIsLoading] = useState(false); 
  const [error, setError] = useState(null); 
  const [currentPage, setCurrentPage] = useState(1);
  const [recipesPerPage] = useState(5);
  const [selectedCuisine, setSelectedCuisine] = useState('');

  const API_KEY = "3beb197c9690416e8301673e7a94b78d";
  var url = `https://api.spoonacular.com/recipes/complexSearch?apiKey=${API_KEY}&query=${query}&number=${recipesPerPage}&offset=${(currentPage - 1) * recipesPerPage}&cuisine=${selectedCuisine}`;

  async function getRecipes() {
    setIsLoading(true);
    setError(null);
    try {
      const result = await Axios.get(url);
      setRecipes(result.data.results);
    } catch (error) {
      setError('Failed to fetch recipes');
      console.error('Error fetching recipes', error);
    } finally {
      setIsLoading(false);
    }
  }

  const onSubmit = (e) => {
    e.preventDefault(); 
    setCurrentPage(1); 
    getRecipes();
  }

  return (
    <div className="app">
      <h1>Recipe Website</h1>
      <form className="app__searchForm" onSubmit={onSubmit}>
        <input 
          type="text" 
          className="app__input"
          placeholder="Enter ingredient" 
          value={query} 
          onChange={(e) => setQuery(e.target.value)}
        />
         <select
          value={selectedCuisine}
          onChange={(e) => setSelectedCuisine(e.target.value)}
          className="app__cuisineSelect"
        >
          <option value="">All Cuisines</option>
          <option value="Italian">Italian</option>
          <option value="Mexican">Mexican</option>
          <option value="Chinese">Chinese</option>
          <option value="Indian">Indian</option>
        </select>
        <input className="app__submit" type="submit" value="Search"/>
      </form>    
      {isLoading && <p>Loading...</p>}
      {error && <p>{error}</p>}
      <div className="app__recipes">
        {recipes.map(recipe => (
          <div key={recipe.id}>
            <h3>{recipe.title}</h3>
            <img src={recipe.image} alt={recipe.title} style={{ width: '100px' }} />
          </div>
        ))}
      </div>
      <div className="pagination">
        <button onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))} disabled={currentPage === 1}>Prev</button>
        <button onClick={() => setCurrentPage(prev => prev + 1)} disabled={recipes.length < recipesPerPage}>Next</button>
      </div>
    </div>
  );
}

export default App;